.dropdown-wrapper{position:relative}.dropdown-wrapper .dropdown__item{display:flex;flex-direction:column;background-color:#373b44;position:absolute;padding:20px;top:75px;left:-40px;color:#fff;border-radius:20px;box-shadow:0px 5px 15px rgba(227,184,115,.2)}.dropdown-wrapper .dropdown__item a{text-decoration:none}.dropdown-wrapper .dropdown__item ul{padding:0;background:#fff;border-radius:5px}.dropdown-wrapper .dropdown__item li{color:rgba(0,0,0,.8);width:246px;background:none;border-radius:5px;margin:0;line-height:21px;padding:7px;background:#fff;word-break:break-all;list-style-type:none}.dropdown-wrapper .dropdown__item li:hover{background-color:#cecece}.dropdown-button::after{position:absolute;right:0;top:160px;content:"Введите пароль указанные при регистрации";border:solid 1px #000;padding:10px 0;border-radius:10px;color:#ff3a3a}.profile-button{margin-top:-15px;border-radius:50px}.dropdown__item-auth{top:75px}.dropdown__item-corner{width:20px;height:20px;background:rgba(0,0,0,.8);transform:rotate(45deg);top:65px;left:28px;position:absolute}@media screen and (max-width: 1300px){.dropdown-wrapper .dropdown__item{left:-100px}}@media screen and (max-width: 1000px){.dropdown-wrapper .dropdown__item{left:-227px}.dropdown-wrapper .dropdown__item.dropdown__item-auth{left:-210px}}@media screen and (max-width: 700px){.dropdown-wrapper{align-self:center}.dropdown-wrapper>button{padding:10px 25px}.dropdown-wrapper .dropdown__item{font-size:20px;left:-220px}.dropdown-wrapper .dropdown__item.dropdown__item-auth li{width:250px}.dropdown-wrapper .dropdown__item input{height:50px;font-size:20px}.dropdown-wrapper .dropdown__item button{height:60px;padding:5px}.dropdown-wrapper .dropdown__item button:first-child{margin-right:10px}.dropdown-wrapper .dropdown__item-corner{left:45px}.profile-button{margin:0 auto}}@media screen and (max-width: 400px){.dropdown-wrapper .dropdown__item{left:-190px}.profile-button{margin:0 auto}}